import React from 'react'

import { NavLink } from 'react-router-dom'
// import Style from 'bootstrap'
import i from '../src/images/w29.png'
import im from '../src/images/w31.png'
import img from '../src/images/w30.png'
import imge from '../src/images/w32.png'

import imges from '../src/images/w33.png'

// import img from '../src/images/w30.png'

const Socialwall = () => {
    return (
        <>
            <section className="container text-center mar">
                    <h1>Social Wall</h1>
                    <div className="color mx-auto">
                        <div className="row g-4">
                            <div className="col-12 col-lg-4  col-md-12">
                            <div className="card" >
  <img src={i} className="card-img-top" alt="Logo"/>
  <div className="card-body">
    <h5 className="card-title"><img src={imge}  alt="Logo"/></h5>
    <p className="card-text"><img src={imges}  alt="Logo"/></p>
    
    
  </div>
</div>
                            </div>
                            <div className="col-12 col-lg-4   col-md-12">
                            <div className="card" >
  <img src={im} className="card-img-top" alt="Logo"/>
  <div className="card-body">
    <h5 className="card-title"><img src={imge} alt="Logo"/></h5>
    <p className="card-text"><img src={imges} alt="Logo"/></p>
    
    
  </div>
</div>
                            </div>
                            <div className="col-12 col-lg-4  col-md-12">
                            <div className="card" >
  <img src={img} className="card-img-top" alt="Logo"/>
  <div className="card-body">
    <h5 className="card-title"><img src={imge}  alt="Logo"/></h5>
    <p className="card-text"><img src={imges}  alt="Logo"/></p>
    
  </div>
</div>
                            </div>

                        </div>
                    </div>

            </section>
        </>
    )
}

export default Socialwall
